import { useLocalStore } from "mobx-react-lite";
import { v4 as uuidv4 } from "uuid";
import { UpdateMessage } from "./useSocketStore";

export type Card = null | number | "selected";
export type User = {
    name?: string,
    card: Card,
    id: string,
    tableId: number | null,
    roomId?: string,
}

export type Table = {
    id: number;
}

export type UserStore = {
    users: User[],
    show: boolean;
    tables: Table[];
    tooltips: { userId: string; message: string; }[];
    removeParticipant: (id: string) => void;
    addParticipant: (user: User) => void;
    update: (message: UpdateMessage["payload"]) => void;
    setShow: (userId?: string) => void;
    setCard: (card: Card) => void;
    reset: (userId?: string) => void;
    login: (name: string, roomId: string | null) => void;
    setSelected: (userId: string) => void;
    logout: () => void;
    setTable: (tableId: number | null) => void;
    addTable: () => void;
    removeTable: (tableId: number) => void;
    addTooltip: (userId: string, message: string) => void;
}

const initialState = {
  users: [
    {
      id: uuidv4(),
      card: null,
      tableId: 0,
    },
  ],
  tables: [{ id: 0 }],
  show: false,
  tooltips: [],
};

export const useUserStore = () => {
  const store = useLocalStore<UserStore>(() => ({
    ...initialState,
    login(name, roomId) {
      store.users[0].roomId = roomId || uuidv4();
      store.users[0].name = name;
    },
    logout() {
      window.location.href = `${window.location.href}?roomId=${store.users[0].roomId}`;

      store.users = initialState.users;
      store.tables = initialState.tables;
      store.show = initialState.show;
    },
    addTooltip(userId: string, message: string) {
      store.tooltips = [{ userId, message }];
    },
    setSelected(id) {
      store.users = store.users.map((p) => {
        if (p.id === id) {
          return {
            ...p,
            card: "selected",
          };
        }
        return p;
      });
    },
    removeParticipant(id) {
      store.users = store.users.filter((p) => p.id !== id);
    },
    addParticipant(user) {
      store.users = [...store.users, {
        ...user,
        tableId: 0,
        card: null,
      }];
    },
    update(message: UpdateMessage["payload"]) {
      const { user, tables } = message;
      const currentUser = store.users.find((p) => p.id === user.id);
      store.users = currentUser
        ? store.users.map((p) => {
          if (p.id === currentUser.id) {
            return {
              ...currentUser,
              ...user,
            };
          }
          return p;
        })
        : [...store.users, user];
      store.tables = tables;
    },
    setShow(userId?: string) {
      store.show = true;
      if (userId) {
        store.addTooltip(userId, "SHOWED");
      }
    },
    reset(userId?: string) {
      store.show = false;
      store.users = store.users.map((p) => {
        p.card = null;
        return p;
      });
      if (userId) {
        store.addTooltip(userId, "CLEARED");
      }
    },
    setCard(card) {
      const me = store.users[0];
      store.users = store.users.map((p) => {
        if (p.id === me.id) {
          p.card = card;
        }
        return p;
      });
    },
    addTable() {
      store.tables = [
        ...store.tables,
        {
          id: store.tables.length,
        },
      ];
    },
    removeTable(tableId: number) {
      store.tables = store.tables.filter((table) => table.id !== tableId);
    },
    setTable(tableId) {
      const me = store.users[0];
      store.users = store.users.map((p) => {
        if (p.id === me.id) {
          p.tableId = tableId;
        }
        return p;
      });
    },
  }));

  return store;
};
