import React from "react";
import { observer } from "mobx-react";
import { globalContext } from "../GlobalProvider";
import { Room } from "./room/Room";
import { Connecting } from "./connecting/Connecting";
import { Login } from "./login/Login";
import { Contact } from "./contact/Contact";
import cn from "./Layout.css";

type Page = "login" | "connecting" | "room";

const Content: React.FC<{ page: Page; }> = ({ page }) => {
  switch (page) {
    case "connecting":
      return <Connecting />;
    case "room":
      return <Room />;
    case "login":
      return <Login />;
    default:
      return <div>PAGE NOT FOUND</div>;
  }
};

export const Layout = observer(() => {
  const [page, setPage] = React.useState<Page>("login");
  const { socketStore } = React.useContext(globalContext);
  const [showContactUs, toggleContactUs] = React.useState(false);
  const { status } = socketStore;

  React.useEffect(() => {
    switch (status) {
      case "connecting":
        setPage("connecting");
        break;
      case "connected":
        setPage("room");
        break;
      default:
        setPage("login");
        break;
    }
  }, [status]);

  const onContactUs = React.useCallback(() => {
    toggleContactUs(!showContactUs);
  }, [showContactUs]);

  return (
    <div className={cn.layout}>
      <Content page={page} />
      <div className={cn.footer}>
        <span className={cn.contactUs} onClick={onContactUs}>Contact Us</span>
      </div>
      <div className={cn.contactUsWrapper}>
        <Contact toggle={onContactUs} show={showContactUs} />
      </div>
    </div>
  );
});
