import React from "react";
import { observer } from "mobx-react";
import cx from "classnames";
import cn from "./FakeTable.css";
import { User } from "~/store/useUserStore";
import { getCoordinates } from "../room/table/Table";

export const FakeTable: React.FC = observer(() => {
  const tableId = -1;
  const users: User[] = [0, 1, 2, 3, 4, 5, 6, 7, 8].map((id: number) => ({
    name: `Player${id}`,
    card: null,
    id: `${id}`,
    tableId,
    roomId: "-1",
  }));
  const table = React.useRef<HTMLDivElement | null>(null);
  const [tableSize, setTableSize] = React.useState<DOMRect | null>(null);

  const handleResize = React.useCallback(() => {
    const el = table.current;
    if (el) {
      const size = el.getBoundingClientRect();
      setTableSize(size);
    }
  }, [table]);

  React.useEffect(() => {
    handleResize();
    window.addEventListener("resize", handleResize, false);
  }, []);

  return (
    <div className={cn.table} ref={table}>
      {
        tableSize && users.map((user, i) => {
          const { width, height } = tableSize;
          return (
            <Seat
              key={`${user.id}-${tableId}`}
              id={users[0].id}
              user={user}
              coordinates={getCoordinates((i / users.length) % 1, width, height)!}
            />
          );
        })
      }
    </div>
  );
});

type SeatProps = {
  coordinates: [number, number];
  user: User;
  id: string;
}
const CARDS = [0.25, 0.5, 1, 2, 3, 4, 5];
const Seat: React.FC<SeatProps> = observer(({
  coordinates, user, id,
}) => {
  const me = user.id === id;
  return (
    <div className={cx(cn.seat)} style={{ top: coordinates[1], left: coordinates[0] }}>
      {
        !me && <span className={cx(cn.name)}>{user.name}</span>
      }
      <ol className={cx(cn.cards, me && cn.me)}>
        {
          me && CARDS.map((c) => (
            <li className={cx(cn.card, me && user.card === c ? cn.selected : "")} key={c}>
              <button disabled={false} type="button">{c}</button>
            </li>
          ))
        }
        {
          !me && (
            <li className={cx(cn.playerCard)}>
              <div className={cx(cn.resultCard)}>?</div>
            </li>
          )
        }
      </ol>
    </div>
  );
});
