import { createContext, ReactNode } from "react";
import * as React from "react";
import { UserStore, useUserStore } from "./store/useUserStore";
import { SocketStore, useSocketStore } from "./store/useSocketStore";

export const globalContext = createContext<{
    userStore: UserStore;
    socketStore: SocketStore;
}>({
  userStore: null as any,
  socketStore: null as any,
});

export const GlobalProvider: React.FC<{ children: ReactNode; }> = ({ children }) => {
  const userStore = useUserStore();
  const socketStore = useSocketStore(userStore);

  return (
    <globalContext.Provider value={{ userStore, socketStore }}>
      {children}
    </globalContext.Provider>
  );
};
