import React from "react";
import cx from "classnames";
import { observer } from "mobx-react";
import cn from "./Contact.css";
import * as config from "../../config.json";

export const Contact: React.FC<{
    toggle: () => void;
    show: boolean;
}> = observer(({ toggle, show }) => {
  const emailRef = React.useRef<HTMLInputElement | null>(null);
  const messageRef = React.useRef<HTMLTextAreaElement | null>(null);

  const onSend = React.useCallback(() => {
    if (emailRef.current?.value && messageRef.current?.value) {
      const requestOptions = {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ email: emailRef.current.value, message: messageRef.current.value }),
      };
      fetch(config.dev.urls.byPath["/contactus"].url, requestOptions)
        .then((response) => response.json())
        .then((data) => {
          console.log(data);
        });
      toggle();
    }
  }, [emailRef.current, messageRef.current]);

  return (
    <div className={cx(cn.contact, show && cn.visible)}>
      <div className={cn.center}>
        <input placeholder="E-mail" type="text" name="email" ref={emailRef} />
        <textarea placeholder="Message..." name="message" ref={messageRef} />
        <button type="button" onClick={onSend} className={cn.send}>SEND</button>
        <button type="button" onClick={toggle}>CLOSE</button>
      </div>
    </div>
  );
});
