import React from "react";
import { observer } from "mobx-react";
import { globalContext } from "../../GlobalProvider";
import cn from "./Login.css";
import { FakeTable } from "./FakeTable";

function getRoomId() {
  const query = new URLSearchParams(window.location.search);
  return query.get("roomId");
}

export const Login = observer(() => {
  const nameRef = React.useRef<HTMLInputElement | null>(null);
  const { userStore, socketStore } = React.useContext(globalContext);
  const { login } = userStore;
  const { connect, setStatus } = socketStore;
  const roomId = getRoomId();

  const onJoin = React.useCallback(() => {
    const name = nameRef.current?.value;
    if (name) {
      setStatus("connecting");
      login(name, roomId);
      window.localStorage.setItem("name", name);
      connect();
    }
  }, [connect, login, nameRef.current]);

  React.useEffect(() => {
    const input = nameRef.current;
    if (input) {
      input.addEventListener("keypress", (e) => {
        if (e.keyCode === 13) {
          onJoin();
        }
      });
      input.focus();
    }
  }, []);

  return (
    <div className={cn.loginContainer}>
      <div className={cn.fakeTable}>
        <FakeTable />
      </div>
      <div className={cn.login}>
        <button type="button" onClick={onJoin}>{roomId ? "JOIN" : "CREATE ROOM"}</button>
        <input placeholder="Your name..." type="text" name="username" defaultValue={window.localStorage.getItem("name")} ref={nameRef} />
      </div>
    </div>
  );
});
