import React from "react";
import { observer } from "mobx-react";
import cx from "classnames";
import { globalContext } from "../../GlobalProvider";
import cn from "./Room.css";
import { Table } from "./table/Table";

export const Room = observer(() => {
  const { userStore, socketStore } = React.useContext(globalContext);
  const { users, tables } = userStore;
  const { addTable } = socketStore;
  const [showInvite, toggleInvite] = React.useState(false);
  const [loaded, setLoaded] = React.useState(false);

  React.useEffect(() => {
    setLoaded(true);
  }, []);

  const inviteURL = window.location.href.indexOf("roomId") !== -1
    ? window.location.href
    : `${window.location.href}?roomId=${users[0].roomId}`;

  return (
    <div className={cn.room}>
      <div className={cx(cn.inviteBar, showInvite && cn.show)}>
        {inviteURL}
      </div>
      <div className={cx(cn.header, loaded && cn.loaded)}>
        <button type="button" className={cn.add} onClick={() => addTable()} disabled={tables.length >= 2}>
          {
            `ADD TABLE ( ${tables.length}/2 )`
          }
        </button>
        <button type="button" className={cn.invite} onClick={() => toggleInvite(!showInvite)}>INVITE PLAYERS</button>
      </div>
      <div className={cn.container}>
        <div className={cn.tables}>
          {
            tables.map((table) => <Table key={table.id} tableId={table.id} />)
          }
        </div>
      </div>
    </div>
  );
});
