import { useLocalStore } from "mobx-react-lite";
import {
  Card, Table, User, UserStore,
} from "./useUserStore";
import * as config from "../config.json";

export type DisconnectedMessage = { type: "disconnected", payload: { id: string, } }
export type ConnectedMessage = { type: "connected", payload: User }
export type UpdateMessage = { type: "update", payload: { user: User; tables: Table[]; } }
export type ShowMessage = { type: "show", payload?: string }
export type ResetMessage = { type: "reset", payload?: string }
export type PingMessage = { type: "ping", payload: string; }
export type PongMessage = { type: "pong", payload: string; }
export type Message = PingMessage
    | PongMessage
    | DisconnectedMessage
    | ConnectedMessage
    | UpdateMessage
    | ShowMessage
    | ResetMessage;

type Status = "not connected" | "connecting" | "connected";
export type SocketStore = {
    socket: WebSocket | null,
    status: Status;
    sendMessage: (message: Message) => void;
    connect: () => void;
    reset: () => void;
    setShow: () => void;
    setCard: (card: Card) => void;
    setStatus: (status: Status) => void;
    update: () => void;
    setTable: (tableId: number | null) => void;
    addTable: () => void;
    removeTable: (tableId: number) => void;
}

const HOST = config.dev.outputs.filter((o) => o.OutputKey === "ServiceEndpointWebsocket")[0].OutputValue;
export const useSocketStore = (userStore: UserStore) => {
  const store = useLocalStore<SocketStore>(() => ({
    socket: null,
    status: "not connected",

    sendMessage(message: Message) {
      if (!store.socket) {
        throw Error("NOT CONNECTED");
      }
      store.socket.send(JSON.stringify({
        action: "sendMessage",
        data: message,
      }));
    },
    setStatus(status) {
      store.status = status;
    },
    connect() {
      const me = userStore.users[0];
      store.socket = new WebSocket(`${HOST}?id=${me.id}&roomId=${me.roomId}&name=${me.name}`);
      store.socket.onmessage = (m) => {
        const message: Message = JSON.parse(m.data);
        console.log(message);
        switch (message.type) {
          case "disconnected":
            userStore.removeParticipant(message.payload.id);
            break;
          case "connected":
            userStore.addParticipant(message.payload);

            store.update();
            if (userStore.show) {
              store.sendMessage({
                type: "show",
              });
            }
            break;
          case "update":
            userStore.update(message.payload);
            break;
          case "show":
            userStore.setShow(message.payload);
            store.update();
            break;
          case "reset":
            userStore.reset(message.payload);
            break;
          case "ping":
            store.sendMessage({ type: "pong", payload: me.id });
            break;
          default:
            break;
        }
      };
      store.socket.onopen = () => {
        store.status = "connected";

        setInterval(() => {
          store.sendMessage({ type: "ping", payload: me.id });
        }, 60000);
      };
      store.socket.onclose = () => {
        userStore.logout();
        store.status = "not connected";
      };
    },
    setShow() {
      const me = userStore.users[0];
      store.sendMessage({ type: "show", payload: me.id });
      userStore.setShow(me.id);
      store.update();
    },
    reset() {
      const me = userStore.users[0];
      store.sendMessage({ type: "reset", payload: me.id });
      userStore.reset(me.id);
    },
    setCard(c) {
      userStore.setCard(c);
      store.update();
    },
    addTable() {
      userStore.addTable();
      store.update();
    },
    removeTable(tableId) {
      userStore.removeTable(tableId);
      store.update();
    },
    setTable(tableId) {
      userStore.setTable(tableId);
      store.update();
    },
    update() {
      const me = userStore.users[0];

      const card = userStore.show
        ? me.card
        : me.card !== null
          ? "selected"
          : null;

      store.sendMessage({
        type: "update",
        payload: {
          user: {
            ...me,
            card,
          },
          tables: userStore.tables,
        },
      });
    },
  }));

  return store;
};
