import React from "react";
import { observer } from "mobx-react";
import cx from "classnames";
import { Card, User, UserStore } from "../../../store/useUserStore";
import { globalContext } from "../../../GlobalProvider";
import cn from "./Table.css";

export const Table: React.FC<{ tableId: number; }> = observer(({ tableId }) => {
  const { userStore, socketStore } = React.useContext(globalContext);
  const {
    setShow, reset, setCard, setTable, removeTable,
  } = socketStore;
  const {
    users, show, tables, tooltips,
  } = userStore;
  const table = React.useRef<HTMLDivElement | null>(null);
  const [tableSize, setTableSize] = React.useState<DOMRect | null>(null);
  const me = users[0];

  const handleResize = React.useCallback(() => {
    const el = table.current;
    if (el) {
      const size = el.getBoundingClientRect();
      setTableSize(size);
    }
  }, [table]);

  React.useEffect(() => {
    handleResize();
  }, [tables]);

  React.useEffect(() => {
    handleResize();
    window.addEventListener("resize", handleResize, false);
  }, []);

  const onChooseCard = React.useCallback((c: Card) => {
    setCard(me.card === c ? null : c);
  }, [me]);

  const onReset = React.useCallback(() => {
    reset();
  }, []);

  const leaveTable = React.useCallback(() => {
    setTable(null);
  }, []);

  const joinTable = React.useCallback(() => {
    setTable(tableId);
  }, []);

  const cardInfo = users.reduce((acc, curr) => {
    if (curr.card && curr.card !== "selected" && curr.tableId === tableId) {
      acc = {
        amount: acc.amount + curr.card,
        userCount: acc.userCount + 1,
      };
    }
    return acc;
  }, { amount: 0, userCount: 0 });
  const avg = cardInfo.userCount ? cardInfo.amount / cardInfo.userCount : 0;

  return (
    <div className={cn.table} ref={table}>
      {
        tableSize && users.filter((user) => user.tableId === tableId).map((user, i) => {
          const { width, height } = tableSize;
          return (
            <Seat
              key={`${user.id}-${tableId}`}
              show={show}
              onChooseCard={onChooseCard}
              id={users[0].id}
              user={user}
              coordinates={getCoordinates((i / users.length) % 1, width, height)!}
              tooltips={tooltips}
            />
          );
        })
      }

      {
        show && (
        <div className={cn.stats}>
          {avg}
        </div>
        )
      }
      <div className={cn.actions}>
        {
          !show && <button type="button" onClick={() => setShow()}>SHOW</button>
        }
        {
          show && <button type="button" onClick={onReset}>RESET</button>
        }
        {
          me.tableId !== tableId && <button type="button" onClick={joinTable}>JOIN</button>
        }
        {
          tableId > 0 && <button type="button" onClick={() => removeTable(tableId)}>DELETE TABLE</button>
        }
      </div>
    </div>
  );
});

type SeatProps = {
    coordinates: [number, number];
    user: User;
    id: string;
    onChooseCard: (card: Card) => void;
    show: boolean;
    tooltips: UserStore["tooltips"];
}
const CARDS = [0.25, 0.5, 1, 1.5, 2, 3, 4, 5];
const Seat: React.FC<SeatProps> = observer(({
  coordinates, user, id, onChooseCard, show,
}) => {
  const me = user.id === id;
  return (
    <div className={cx(cn.seat)} style={{ top: coordinates[1], left: coordinates[0] }}>
      {
        !me && <span className={cx(cn.name)}>{user.name}</span>
      }
      <ol className={cx(cn.cards, me && cn.me)}>
        {
          me && CARDS.map((c) => (
            <li className={cx(cn.card, me && user.card === c ? cn.selected : "")} key={c}>
              <button disabled={show} type="button" onClick={() => onChooseCard(c)}>{c}</button>
            </li>
          ))
        }
        {
          !me && (
            // TODO Some weird shit going on here. Refactor.
            <li className={cx(cn.playerCard)}>
              <div
                style={{ position: "absolute" }}
                className={cx(
                  user.card ? cn.selected : cn.resultCard,
                  (user.card && user.card !== "selected") ? cn.hidden : cn.visible,
                )}
              >
                ?
              </div>
              <div className={cx(cn.faces, show && cn.flipped)}>
                <div className={cx(
                  cn.face, cn.front, cn.selected,
                  (user.card && user.card !== "selected") ? cn.visible : cn.hidden,
                )}
                >
                  ?
                </div>
                <div className={cx(
                  cn.face, cn.back, cn.resultCard,
                  (user.card && user.card !== "selected") ? cn.visible : cn.hidden,
                )}
                >
                  {user.card}
                </div>
              </div>
            </li>
          )
        }
      </ol>
    </div>
  );
});

export function getCoordinates(p: number, width: number, height: number): [number, number] | undefined {
  const r = Math.min(width, height) / 2;
  const cornerLength = (Math.PI * r) / 2;
  const sideLengthX = width > height ? width - height : 0;
  const sideLengthY = width > height ? 0 : height - width;
  const length = sideLengthX * 2 + sideLengthY * 2 + cornerLength * 4;

  // bottom straight (left half of it)
  const p1 = (sideLengthX / 2) / length;
  if (p1 > 0 && p < p1) {
    const straightP = p / p1;
    const start = [width / 2, height];
    const end = [r, height];
    return [
      (end[0] - start[0]) * straightP + start[0],
      (end[1] - start[1]) * straightP + start[1],
    ];
  }

  // bottom left
  const p2 = p1 + cornerLength / length;
  if (p <= p2) {
    const circleP = (p - p1) / (p2 - p1);
    const start = [r, height];
    return [
      start[0] - Math.sin((circleP * Math.PI) / 2) * r,
      start[1] - (1 - Math.cos((circleP * Math.PI) / 2)) * r,
    ];
  }

  // left straight
  const p3 = p2 + sideLengthY / length;
  if (p < p3) {
    const straightP = (p - p2) / (p3 - p2);
    const start = [0, height - r];
    const end = [0, r];
    return [
      (end[0] - start[0]) * straightP + start[0],
      (end[1] - start[1]) * straightP + start[1],
    ];
  }

  // top left
  const p4 = p3 + cornerLength / length;
  if (p < p4) {
    const circleP = (p - p3) / (p4 - p3);
    const start = [0, r];
    return [
      start[0] + (1 - Math.cos((circleP * Math.PI) / 2)) * r,
      start[1] - Math.sin((circleP * Math.PI) / 2) * r,
    ];
  }

  // top straight
  const p5 = p4 + sideLengthX / length;
  if (p <= p5) {
    const straightP = (p - p4) / (p5 - p4);
    const start = [r, 0];
    const end = [width - r, 0];
    return [
      (end[0] - start[0]) * straightP + start[0],
      (end[1] - start[1]) * straightP + start[1],
    ];
  }

  // top right
  const p6 = p5 + cornerLength / length;
  if (p < p6) {
    const circleP = (p - p5) / (p6 - p5);
    const start = [width - r, 0];
    return [
      start[0] + Math.sin((circleP * Math.PI) / 2) * r,
      start[1] + (1 - Math.cos((circleP * Math.PI) / 2)) * r,
    ];
  }

  // right straight
  const p7 = p6 + sideLengthY / length;
  if (p < p7) {
    const straightP = (p - p6) / (p7 - p6);
    const start = [width, r];
    const end = [width, height - r];
    return [
      (end[0] - start[0]) * straightP + start[0],
      (end[1] - start[1]) * straightP + start[1],
    ];
  }

  // bottom right
  const p8 = p7 + cornerLength / length;
  if (p <= p8) {
    const circleP = (p - p7) / (p8 - p7);
    const start = [width, height - r];
    return [
      start[0] - (1 - Math.cos((circleP * Math.PI) / 2)) * r,
      start[1] + Math.sin((circleP * Math.PI) / 2) * r,
    ];
  }

  // bottom straight (right half of it)
  const p9 = 1;
  if (p <= p9) {
    const straightP = (p - p8) / (p9 - p8);
    const start = [width - r, height];
    const end = [width / 2, height];
    return [
      (end[0] - start[0]) * straightP + start[0],
      (end[1] - start[1]) * straightP + start[1],
    ];
  }

  return undefined;
}
